import React from "react";
// react component for creating dynamic tables
import ReactTable from "react-table";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
import Assignment from "@material-ui/icons/Assignment";
import Dvr from "@material-ui/icons/Dvr";
import Favorite from "@material-ui/icons/Favorite";
import Close from "@material-ui/icons/Close";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardIcon from "components/Card/CardIcon.js";
import CardHeader from "components/Card/CardHeader.js";

import { dataTable } from "variables/publisherreportdatatable.js";

import { cardTitle } from "assets/jss/material-dashboard-pro-react.js";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Hidden from "@material-ui/core/Hidden";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import InputLabel from "@material-ui/core/InputLabel";
import customSelectStyle from "assets/jss/material-dashboard-pro-react/customSelectStyle.js";
import FormControl from "@material-ui/core/FormControl";
// material-ui icons
import Menu from "@material-ui/icons/Menu";
import MButton from '@material-ui/core/Button';
import Publishicon from '@material-ui/icons/RecordVoiceOver';

const styles = {
  cardIconTitle: {
    ...cardTitle,
    marginTop: "15px",
    marginBottom: "0px"
  }
};

const useStyles = makeStyles(styles);
function filterCaseInsensitive(filter, row) {

  const id = filter.pivotId || filter.id;
  if (row[id] !== null) {
      return (
          row[id] !== undefined ?
              String(row[id].toString().toLowerCase())
                  .includes(filter.value.toString().toLowerCase())
          :
              true
      );
  }
}
export default function ReactTables() {
  const [data, setData] = React.useState(
    dataTable.dataRows.map((prop, key) => {
      return {
        id: key,
        Name: prop[0],
        Date: prop[1],
        Revenue: prop[2],
        Impression: prop[3],
        eCPM: prop[4]
      
      };
    })
  );
  const classes = useStyles();
  return (
    <GridContainer>
      <GridItem xs={12}>
        <Card>
          <CardHeader color="primary" icon>
              
          <CardIcon color="primary">
         <Publishicon style={{color:"white"}} />
         
         </CardIcon>
            <h4 className={classes.cardIconTitle} style={{marginTop:"0px!important"}} >Publishers Report</h4>
            
              <FormControl fullWidth className={classes.selectFormControl} style={{width:"30%",float:"right",paddingRight :"20px",marginTop:"-50px"}}>
              <InputLabel style={{fontSize:"14px",color:"#4e4b4b",fontWeight:"400"}}
                    htmlFor="simple-select"
                    className={classes.selectLabel}
                  >
                Select 
                  </InputLabel> 
           <Select 
             MenuProps={{
               className: classes.selectMenu
             }}
             classes={{
               select: classes.select
             }}
            
           >
             <MenuItem style={{fontSize:"14px"}}
              
               classes={{
                root: classes.selectMenuItem,
                selected: classes.selectMenuItemSelected
               }}
                value="Last 7 Days"
             >
              Last 7 Days
             </MenuItem>
             <MenuItem style={{fontSize:"14px"}}
               classes={{
                 root: classes.selectMenuItem,
                 selected: classes.selectMenuItemSelected
               }}
               value="This Month"
             >
              This Month
             </MenuItem>
             <MenuItem style={{fontSize:"14px"}}
               classes={{
                 root: classes.selectMenuItem,
                 selected: classes.selectMenuItemSelected
               }}
               value="Last Month"
             >
               Last Month
             </MenuItem>
             <MenuItem style={{fontSize:"14px"}}
               classes={{
                 root: classes.selectMenuItem,
                 selected: classes.selectMenuItemSelected
               }}
               value="Custom Date"
             >
              Custom Date
             </MenuItem>
           </Select>
         </FormControl>
         
          </CardHeader>
          <CardBody>
            <ReactTable
              data={data}
              filterable
              columns={[
                {
                  Header: "Name",
                  accessor: "Name",
                  Filter: ({filter, onChange}) => (
                    <input type='text' style={{textAlign:'center'}}
                           placeholder="Search Publishers"
                           value={filter ? filter.value : ''}
                               onChange={event => onChange(event.target.value)}
                    />
                  ),
                  Footer:(
                    <span><strong>Total</strong></span>
                  )
                },
                {
                  Header: "Date",
                  accessor: "Date",
                  Filter: ({filter, onChange}) => (
                    <input type='text' style={{textAlign:'center'}}
                           placeholder="Search Date"
                           value={filter ? filter.value : ''}
                               onChange={event => onChange(event.target.value)}
                    />
                  ),
                  Footer:(
                    <span><strong></strong></span>
                  )
                },
                {
                  Header: "Revenue",
                  accessor: "Revenue",
                  Filter: ({filter, onChange}) => (
                    <input type='text' style={{textAlign:'center'}}
                           placeholder="Search Revenue"
                           value={filter ? filter.value : ''}
                               onChange={event => onChange(event.target.value)}
                    />
                  ),
                  Footer:(
                    <span><strong>$9.44</strong></span>
                  )
                },
                {
                  Header: "Impression",
                  accessor: "Impression",
                  Filter: ({filter, onChange}) => (
                    <input type='text' style={{textAlign:'center'}}
                           placeholder="Search Impression"
                           value={filter ? filter.value : ''}
                               onChange={event => onChange(event.target.value)}
                    />
                  ),
                  Footer:(
                    <span><strong>58.4K</strong></span>
                  )
                },
                {
                  Header: "eCPM",
                  accessor: "eCPM",
                  Filter: ({filter, onChange}) => (
                    <input type='text' style={{textAlign:'center'}}
                           placeholder="Search eCPM"
                           value={filter ? filter.value : ''}
                               onChange={event => onChange(event.target.value)}
                    />
                  ),
                  Footer:(
                    <span><strong>$0.16</strong></span>
                  )
                  
                }
              ]}
              defaultPageSize={5}
              defaultFilterMethod={filterCaseInsensitive}
              showPaginationTop 
              showPaginationBottom={false}
              className="-highlight"
            />
          </CardBody>
          <CardHeader style={{float:"right!important"}}>
            <MButton color="secondary" variant="outlined" style={{float:"right"}}>
           Download
           </MButton> 
          </CardHeader>
        </Card>
      </GridItem>
    </GridContainer>
  );
}
