import React, { Component } from 'react'
import { makeStyles } from "@material-ui/core/styles";

// material-ui icons
import Assignment from "@material-ui/icons/Assignment";
import axios from 'axios';
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardIcon from "components/Card/CardIcon.js";
import CardBody from "components/Card/CardBody.js";
import { SERVER_URL } from "../../variables/constants";
import { cardTitle } from "assets/jss/material-dashboard-pro-react.js";
import Button from "components/CustomButtons/Button.js";
import SlidingPane from "react-sliding-pane";
import "react-sliding-pane/dist/react-sliding-pane.css";
import { withStyles } from '@material-ui/styles';
import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps } from '../../utils/MapStateDispatchProps.js'

const panelStyles = {
    panelClass: {
      zIndex:999
    },
    ModalStyle: {
      width:'100% !important',
      backgroundColor:'#ddd'
    },
    viewSectionsModalStyle: {
      width:'60% !important',
      backgroundColor:'#ddd'
    },
    viewAssignmentModalStyle: {
      width:'40% !important',
      backgroundColor:'#ddd'
    },
  };

const styles = {
    customCardContentClass: {
        paddingLeft: "0",
        paddingRight: "0"
    },
    cardIconTitle: {
        ...cardTitle,
        marginTop: "15px",
        marginBottom: "0px"
    }
};

const useStyles = makeStyles(styles);
export class AdsTxtManager extends Component {
    state = {
        websiteHeaders : ["Name","Url" , "Status" , "Last checked Date","View records"],
        websiteData: [],
        currentSelectedWebsite : '',
        adsTxtRecords : [],
        strAdsTxtRecord : "",
        isPaneOpen : false,
        strHtmlxtRecord : ""
    }

    viewAdsTxtForWebsite = (id,hostURL) =>{

        const TOKEN = 'Bearer '.concat(this.props.data.token);

        axios.defaults.headers.common['Authorization'] = TOKEN;
        axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Authorization';
        
        axios.get(`${SERVER_URL}/api/adstxt/website/${id}`, { headers: { "Authorization": TOKEN } })
            .then(res => {
                let strAdsTxtRecord = "";
                let adsTxtRecords = res.data;
                let adsHtmlRecords = "<ol>";
                adsTxtRecords.forEach(record =>{
                    let strRecord = record.exchange_domain_name + "," + record.account_id;
                    if(record.account_type)
                        strRecord = strRecord + "," + record.account_type;
                    if(record.certification_auth_id){
                        strRecord = strRecord + "," + record.certification_auth_id;
                        strAdsTxtRecord = strAdsTxtRecord + "\n" + strRecord;                        
                    }else{
                        strAdsTxtRecord = strAdsTxtRecord + "\n" + strRecord;
                    }
                    adsHtmlRecords = adsHtmlRecords + "<li>" + strRecord + "</li>";
                });
                
                adsHtmlRecords = adsHtmlRecords + "</ol>";
                this.setState({
                    strHtmlxtRecord : adsHtmlRecords,
                    adsTxtRecords:res.data,
                    currentSelectedWebsite : hostURL,
                    isPaneOpen : true,
                    strAdsTxtRecord : strAdsTxtRecord
                });
            }).catch(function (error) {
                console.log(error);
            });

    }
    

    componentDidMount() {
        const classes = this.props.classes;
        const USER_ID = this.props.data.id;
        const TOKEN = 'Bearer '.concat(this.props.data.token);

        axios.defaults.headers.common['Authorization'] = TOKEN;
        axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Authorization';
        let url = "";
        if(this.props.data.idRole == 3)
            url = `${SERVER_URL}/api/userwebsite/user/${USER_ID}`;
        else if(this.props.data.idRole == 1 || this.props.data.idRole == 4)  {
            url = `${SERVER_URL}/api/userwebsite/`;
        }  
        axios.get(url, { headers: { "Authorization": TOKEN } })
            .then(res => {
                let lWebsiteData = [];
                res.data.forEach(item =>{
                    let lWebsite = [];
                    lWebsite.push(item.name);
                    lWebsite.push(item.hostURL);
                    lWebsite.push(item.adsTxtStatus);
                    lWebsite.push(item.adsTxtStatusDate);
                    lWebsite.push(
                    <Button round  color = "red" className={classes.actionButton + " " + classes.actionButtonRound} key={item.id} onClick={() => this.viewAdsTxtForWebsite(item.id,item.hostURL)}>
                         View
                    </Button>
                    );
                    lWebsiteData.push(lWebsite);
                });
                this.setState({websiteData:lWebsiteData});
            }).catch(function (error) {
                console.log(error);
            });
    }
    render() {


        const classes = this.props.classes;
        return (
            <div>
            <GridContainer>
                <GridItem xs={12}>
                    <Card>
                        <CardHeader color="rose" icon>
                            <CardIcon color="rose">
                                <Assignment />
                            </CardIcon>
                            <h4 className={classes.cardIconTitle}>Websites</h4>
                        </CardHeader>
                        <CardBody>
                            <Table
                                tableHeaderColor="primary"
                                tableHead={this.state.websiteHeaders}
                                tableData={this.state.websiteData}
                                coloredColls={[3]}
                                colorsColls={["primary"]}
                            />
                        </CardBody>
                    </Card>
                </GridItem>
                
            </GridContainer >
            <SlidingPane
            className={classes.ModalStyle +" panelHeaderClass"}
            overlayClassName={classes.panelClass}
            isOpen={this.state.isPaneOpen}
            title="Ads-Txt Records"
            subtitle=""
            onRequestClose={() => {
              this.setState({ isPaneOpen: false });
            }}
          >

          {this.state.strHtmlxtRecord}

          </SlidingPane>
            </div>
        )
    }
}

const AdsTxtManagerHOC = withStyles(styles)(AdsTxtManager);
export default connect(mapStateToProps, mapDispatchToProps)(AdsTxtManagerHOC);