import Globe from "@material-ui/icons/Public";
import Bankicon from "@material-ui/icons/AccountBalance";
import Profileicon from "@material-ui/icons/Person"
import LeftAorrow from '@material-ui/icons/KeyboardArrowLeft';
import Mappingicon from '@material-ui/icons/LinearScale';
import Tooltip from '@material-ui/core/Tooltip';
const LightTooltip = withStyles((theme) => ({
    tooltip: {
      backgroundColor: theme.palette.common.white,
      color: 'rgba(0, 0, 0, 0.87)',
      boxShadow: theme.shadows[1],
      fontSize: 11,
    },
  }))(Tooltip);

const styles = {
  cardIconTitle: {
    ...cardTitle,
    marginTop: "15px",
    marginBottom: "0px"
  },
  rootslider :{ 
    '& .MuiToolbar-gutters' :{
      marginLeft:"24px",
      marginRight:"24px"
    }, 
   '& .makeStyles-SliderTitle-167  ' :{
     fontSize:"18px!important"
  },
      '& .MuiFormControlLabel-root' :{ 
          paddingTop:"6px!important" ,
          marginRight:"15px!important",
         
         },
         '& .MuiTypography-body1' :{
          fontSize:"15px"
         },
      
      '& .MuiGrid-container' :{
        paddingLeft:"6px",
        paddingRight:"6px"
      },
      
      '& .MuiInputLabel-outlined.MuiInputLabel-shrink' :{
        transform:"translate(14px, -6px) scale(0.75)"
      },
      '& .MuiInputLabel-outlined' : { 
        transform: "translate(14px, 14px) scale(1)",
        fontSize:"0.9rem !important"
      }, 
      '& .MuiOutlinedInput-input':{
        paddingTop: "10px", 
         paddingBottom: "10px",
         paddingLeft: "10px",
      paddingRight:"10px"
      },
      '& .MuiFormControl-root ' :{
        width: "100%",
        marginTop:"10px",
        marginBottom:"10px",
        marginLeft:"0px",
        marginRight:"0px",
       
    
    },
    '& .makeStyles-grid-166' :{
      padding: "0 5px !important"
    },
    '& .MuiOutlinedInput-adornedStart' :{
      paddingLeft:"10px"
    },
    '& .makeStyles-grid-167' :{
      padding: "0 5px !important"
    },
    
    
    '& #document-type' :{
      textAlign:"center!important"
    },
      '& .MuiButton-label' :{
          fontSize:"0.875rem",
          fontWeight:"bold"
      },
      '& .MuiPaper-root':{
          backgroundColor: "#dddddd!important"
        },
      '& .MuiAppBar-positionFixed':{
          top:"0",
          left: "auto",
          right: "0",
          position:" relative!important",
          boxShadow:"none!important"
       
      },
      
        '& .MuiToolbar-regular': {
          minHeight:"64px!important",
          overflowX:" hidden !important"
      },
      '& .MuiDialog-paperFullScreen' :{
          backgroundColor: "#dddddd!important",
          overflowX:" hidden !important"
      },
      '& .MuiPaper-elevation4' :{
          borderBottom:"1px solid rgb(0 0 0 / 10%) !important",
          background: "#ebebeb !important",
          boxShadow:"none!important"
      }
  },
  CardHeading:{
    textAlign:"center",
    color:"black!important",
},
MainHeading:{
    textAlign:"left",
    color:"#3c4858!important",
    
},
CloseButton :{
    backgroundColor:"white",
    borderRadius:"30px",
    color:"black",
    width: "41px",
    height: "41px", 
    
    minWidth: "41px", 
    paddingLeft: "12px",
    paddingRight: "12px"
},
SliderTitle :{
    fontSize:"16px",
    paddingLeft:"30px",
    color:"black !important",
    fontWeight:"400"

},
CustomappBar :{
    display: "flex",
    flex:" 0 0 64px",
    background: "#ebebeb",
    height:" 64px",
    borderBottom: "1px solid rgb(0 0 0 / 10%)",
    
   
},
heading:{
    color:"black!important"
  },
SliderBackground :{
    backgroundColor: "#dddddd!important"
},
TextCenter:{
    textAlign:"center"
},
SmallText:{
    fontSize:"12px",
    color:"#000000de",
    fontWeight:"300"
},
headingStyle: {
    marginBottom: "0px",
    fontSize: "1.1em",
    fontWeight: "500",
    marginTop: "25px",
  },
root2: {
    
      float: "right"
    },
    checkboxroot :{
        '& .MuiSvgIcon-root' :{
          width: "0.7em",
          height:"0.7em"
         },
      },
    ActionsButton :{
        '& .MuiButton-root' :{
            minWidth:" 0px"
        },
        '& .MuiButton-root' :{
            height: "0px"
        }
        
    },
    root2: {
        float: "right",
        Primary:{
          color: "#1976d2",
          border: "1px solid #1976d2"
      }
    }
};



const useStyles = makeStyles(styles);
const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="left" ref={ref} {...props} />;
  });
export default function Publisher() {
const [open, setOpen] = React.useState(false);
  const [data, setData] = React.useState(
    dataTable.dataRows.map((prop, key) => {
      return {
        id: key,
        advertiserName: prop[0],
        contactPersonName: prop[1],
        contactPersonNumber: prop[2],
        email: prop[3],
        actions: prop[4],
      };
    })
  );
  const classes = useStyles();
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false); 
  };
  return (
      
    <GridContainer>
      
      <GridItem lg={12}>
        <Card>
          <CardHeader color="primary" icon>
          <CardIcon color="primary">
         <Advertisericon style={{color:"white"}} />
         
         </CardIcon>
            <h4 className={classes.cardIconTitle} style={{marginTop:"0px!important"}} >Advertisers</h4>
            
          </CardHeader>
          <CardBody> 
            <ReactTable
              data={data}
              filterable
              columns={[
                {
                    Header: "Advertiser Name",
                    accessor: "advertiserName",
                    Filter: ({filter, onChange}) => (
                        <input type='text' style={{textAlign:'center'}}
                               placeholder="Search Name"
                               value={filter ? filter.value : ''}
                                   onChange={event => onChange(event.target.value)}
                        />
                      ),
                },
                {
                    Header: "Contact Person",
                    accessor: "contactPersonName",
                    Filter: ({filter, onChange}) => (
                        <input type='text' style={{textAlign:'center'}}
                               placeholder="Search Person"
                               value={filter ? filter.value : ''}
                                   onChange={event => onChange(event.target.value)}
                        />
                      ),
                },
                {
                    Header: "Phone Number",
                    accessor: "contactPersonNumber",
                    Filter: ({filter, onChange}) => (
                        <input type='text' style={{textAlign:'center'}}
                               placeholder="Search Phone Number"
                               value={filter ? filter.value : ''}
                                   onChange={event => onChange(event.target.value)}
                        />
                      ),
                },
                {
                    Header: "Email",
                    accessor: "email",
                    Filter: ({filter, onChange}) => (
                        <input type='text' style={{textAlign:'center'}}
                               placeholder="Search Email Id"
                               value={filter ? filter.value : ''}
                                   onChange={event => onChange(event.target.value)}
                        />
                      ),
                },
                {
                    Header: "Actions",
                    accessor: "actions",
                    Cell: id => (
                        <div className={classes.ActionsButton}>
                            <LightTooltip  title="View" aria-label="view"  placement="top">
                        <MButton onClick={handleClickOpen}
                        justIcon
                        round
                        simple
                        color="secondary"
                          className="view"
                          title="View"
                      >
                         
                        <View />
                        </MButton>
               </LightTooltip >
                        <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition} className={classes.rootslider}>
                    <AppBar className={classes.CustomappBar}>
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close" className={classes.CloseButton}>
                        <LeftAorrow />
                        </IconButton>
                        <h4 className={classes.SliderTitle}>
                      Advertise View
                        </h4>
                    
                    </Toolbar>
                    </AppBar>
                    </Dialog>
                     
                      {" "}
                    {/* use this button to remove the data row */}
                    <LightTooltip  title="Disabled / Unable" aria-label="Disableunable"  placement="top">
                    <Switch
                        name="Disableunable"
                        inputProps={{ 'aria-label': 'secondary checkbox' }}
                        size="small"
                    />
                    </LightTooltip >
                    </div>
                    ),
                    sortable: false,
                    filterable: false
                }
            ]}
            defaultPageSize={5}
            showPaginationTop
            showPaginationBottom={false}
            className="-highlight"
            />
          </CardBody>
          <CardHeader style={{float:"right!important"}}>
          <div className={classes.root2} >
           <span style={{paddingLeft:"10px"}}><MButton color="secondary" variant="outlined">
            Download
           </MButton></span>
           <span style={{paddingLeft:"10px"}}> <MButton  color="secondary" variant="outlined" >
            Upload
           </MButton></span>
          </div>
          </CardHeader>
        </Card>
      </GridItem>
     
    </GridContainer>
  );
}
