import React, { Component } from "react";
// react component for creating dynamic tables
import ReactTable from "react-table";

// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons
import Assignment from "@material-ui/icons/Assignment";
import Globe from "@material-ui/icons/Language";
import Dvr from "@material-ui/icons/Dvr";
import Favorite from "@material-ui/icons/Favorite";
import Close from "@material-ui/icons/Close";
import axios from 'axios';
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardIcon from "components/Card/CardIcon.js";
import CardHeader from "components/Card/CardHeader.js";
import { withStyles } from '@material-ui/styles';
import { connect } from 'react-redux';
import { SERVER_URL } from "../../variables/constants";
import { mapStateToProps, mapDispatchToProps } from '../../utils/MapStateDispatchProps.js'

import { cardTitle } from "assets/jss/material-dashboard-pro-react.js";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Hidden from "@material-ui/core/Hidden";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import InputLabel from "@material-ui/core/InputLabel";
import customSelectStyle from "assets/jss/material-dashboard-pro-react/customSelectStyle.js";
import FormControl from "@material-ui/core/FormControl";
import CardFooter from "components/Card/CardFooter.js";
// material-ui icons
import Menu from "@material-ui/icons/Menu";
import MButton from '@material-ui/core/Button';

const styles = {
  cardIconTitle: {
    ...cardTitle,
    marginTop: "3px",
    marginBottom: "0px"
  },

  rootselect:{
    fontSize:"15px !important",
    fontWeight:"400 !important",
    textAlign:"left !important"
  },
};

const useStyles = makeStyles(styles);
function filterCaseInsensitive(filter, row) {

  const id = filter.pivotId || filter.id;
  if (row[id] !== null) {
    return (
      row[id] !== undefined ?
        String(row[id].toString().toLowerCase())
          .includes(filter.value.toString().toLowerCase())
        :
        true
    );
  }
}
const dataTable = {
  dataRows: []
};
class Sitereport extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selectWebsiteRevenueDuration: 'Last7Days',
      totalRevenue: 0.0,
      totalImpressions: 0,
      totaleCpm: 0.0,
      data: dataTable.dataRows.map((prop, key) => {
        return {
          id: key,
          Sites: prop[0],
          Date: prop[1],
          Revenue: prop[2],
          Impression: prop[3],
          eCPM: prop[4]
        };
      })
    };
  }

  componentDidMount = () => {
    this.loadSiteWiseData();

  }

  loadSiteWiseData = () => {
    const USER_ID = this.props.data.id;
    const USER_ID_ROLE = this.props.data.idRole;
    const TOKEN = 'Bearer '.concat(this.props.data.token);
    var url = "";
    if (USER_ID_ROLE == 1 || USER_ID_ROLE == 4) {
      //url = `${SERVER_URL}/api/users/admin/dashboard/${USER_ID}`;
    } else {
      url = `${SERVER_URL}/api/users/admin/dashboard/websitewisereport/${USER_ID}`;
    }

    //axios.get(url, { headers: { "Authorization": TOKEN } })
    axios.defaults.headers.common['Authorization'] = TOKEN;
    axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Authorization';
    var requestData = {};
    requestData.reportDuration = this.state.selectWebsiteRevenueDuration;
    axios.post(url, requestData, { headers: { "Authorization": TOKEN } })
      .then(res => {
        let lTotalImpressions = 0;
        let lTotalRevenue = 0;
        let lData = res.data.map((prop, key) => {
          var lRevenue =  Number(prop[2]);
          var lImpressions =  Number(prop[3]);
          lTotalImpressions = lTotalImpressions + lImpressions;
          lTotalRevenue = lTotalRevenue + lRevenue;
          var eCpm = (lRevenue / (lImpressions / 1000)).toFixed(2);
          return {
            id: key,
            Sites: prop[0],
            Date: prop[1],
            Revenue: prop[2],
            Impression: prop[3],
            eCPM:eCpm
          };
        })
        let lTotalECpm = ( lTotalRevenue / (lTotalImpressions / 1000)).toFixed(2);
        this.setState({
          data : lData,
          totalImpressions : lTotalImpressions,
          totalRevenue : lTotalRevenue.toFixed(2),
          totaleCpm : lTotalECpm
        });

      }).catch(function (error) {
        console.log(error);
      });
  }

  handleWebsiteSelect = (event) => {
    this.setState({ 'selectWebsiteRevenueDuration': event.target.value });
  }
  downloadReport = () => {
    const USER_ID = this.props.data.id;
    const TOKEN = 'Bearer '.concat(this.props.data.token);
    axios.defaults.headers.common['Authorization'] = TOKEN;
    axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Authorization';
    var requestData = {};
    requestData.reportDuration = this.state.selectWebsiteRevenueDuration;
    axios.post(`${SERVER_URL}/api/users/admin/dashboard/websitewisereport/download/${USER_ID}`, requestData, { headers: { "Authorization": TOKEN, responseType: 'blob' } })
            .then(response => {
                let fileName = "report.xlsx";
                if (window.navigator && window.navigator.msSaveOrOpenBlob) { // IE variant
                    window.navigator.msSaveOrOpenBlob(new Blob([response.data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }),
                        fileName);
                } else {
                    const url = window.URL.createObjectURL(new Blob([response.data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }));
                    const link = document.createElement('a');
                    link.href = url;
                    link.setAttribute('download', fileName);
                    document.body.appendChild(link);
                    link.click();
                }
            }).catch(function (error) {
                console.log(error);
            });
  }


  render() {
    const classes = this.props.classes;
    return <GridContainer>
      <GridItem xs={12}  >
        <Card>
          <CardHeader  color="primary" icon>
            <CardIcon color="primary">
              <Globe style={{ color: "white" }} />

            </CardIcon>

            <h4 className={classes.cardIconTitlecustom} style={{ marginTop: "0px!important" }} >Sites Report</h4>

            <FormControl fullWidth className={classes.selectFormControl} style={{ width: "30%", float: "right", paddingRight: "20px", marginTop: "-25px" }}>
            
              <Select className={classes.rootselect} 
                MenuProps={{
                  className: classes.selectMenu
                }}
                classes={{
                  select: classes.select
                }}
                value={this.state.selectWebsiteRevenueDuration}
                onChange={this.handleWebsiteSelect}
              >
                <MenuItem style={{ fontSize: "14px"}}

                            classes={{
                              root: classes.selectMenuItem,
                              selected: classes.selectMenuItemSelected
                            }}
                            value="Today" 
                          >
                            Today
                        </MenuItem>
                          <MenuItem style={{ fontSize: "14px" }}

                            classes={{
                              root: classes.selectMenuItem,
                              selected: classes.selectMenuItemSelected
                            }}
                            value="Yesterday"
                          >
                            Yesterday
                       </MenuItem>
                          <MenuItem style={{ fontSize: "14px" }}
                            classes={{
                              root: classes.selectMenuItem,
                              selected: classes.selectMenuItemSelected
                            }}
                            value="Last7Days"
                          >
                            Last 7 Days
                         </MenuItem>
                          <MenuItem style={{ fontSize: "14px" }}
                            classes={{
                              root: classes.selectMenuItem,
                              selected: classes.selectMenuItemSelected
                            }}
                            value="ThisMonth"
                          >
                            This Month
                    </MenuItem>
                    <MenuItem style={{ fontSize: "14px" }}
                            classes={{
                              root: classes.selectMenuItem,
                              selected: classes.selectMenuItemSelected
                            }}
                            value="LastMonth"
                          >
                            Last Month
                    </MenuItem>
                {/*<MenuItem style={{fontSize:"14px"}}
             classes={{
               root: classes.selectMenuItem,
               selected: classes.selectMenuItemSelected
             }}
             value="Custom Date"
           >
            Custom Date
            </MenuItem>*/}
              </Select>
            </FormControl>

          </CardHeader>
          <CardBody>
            <ReactTable
              data={this.state.data}
              filterable
              columns={[
                {
                  Header: "Sites",
                  accessor: "Sites",
                  Filter: ({ filter, onChange }) => (
                    <input type='text' style={{ textAlign: 'center' }}
                      placeholder="Search Sites"
                      value={filter ? filter.value : ''}
                      onChange={event => onChange(event.target.value)}
                    />
                  ),
                  Footer: (
                    <span><strong>Total</strong></span>
                  )



                },
                {
                  Header: "Date",
                  accessor: "Date",
                  Filter: ({ filter, onChange }) => (
                    <input type='text' style={{ textAlign: 'center' }}
                      placeholder="Search Date"
                      value={filter ? filter.value : ''}
                      onChange={event => onChange(event.target.value)}
                    />
                  ),
                  Footer: (
                    <span><strong></strong></span>
                  )


                },
                {
                  Header: "Revenue",
                  accessor: "Revenue",
                  Filter: ({ filter, onChange }) => (
                    <input type='text' style={{ textAlign: 'center' }}
                      placeholder="Search Revenue"
                      value={filter ? filter.value : ''}
                      onChange={event => onChange(event.target.value)}
                    />
                  ),

                  Footer: (
                    <span><strong>${this.state.totalRevenue}</strong></span>
                  )

                },
                {
                  Header: "Impression",
                  accessor: "Impression",
                  Filter: ({ filter, onChange }) => (
                    <input type='text' style={{ textAlign: 'center' }}
                      placeholder="Search Impression"
                      value={filter ? filter.value : ''}
                      onChange={event => onChange(event.target.value)}
                    />
                  ),

                  Footer: (
                    <span><strong>{this.state.totalImpressions}</strong></span>
                  )
                },
                {
                  Header: "eCPM",
                  accessor: "eCPM",
                  Filter: ({ filter, onChange }) => (
                    <input type='text' style={{ textAlign: 'center' }}
                      placeholder="Search eCPM"
                      value={filter ? filter.value : ''}
                      onChange={event => onChange(event.target.value)}
                    />
                  ),
                  Footer: (
                    <span><strong>${this.state.totaleCpm}</strong></span>
                  )

                }
              ]}
              defaultPageSize={5}
              defaultFilterMethod={filterCaseInsensitive}
              showPaginationTop
              showPaginationBottom={false}
              className="-highlight"
            />
          </CardBody>
          <CardHeader style={{ float: "right!important" }}>
            <MButton onClick={this.downloadReport} color="secondary" variant="outlined" style={{ float: "right" }}>
              Download
         </MButton>
          </CardHeader>
        </Card>
      </GridItem>s
    </GridContainer>
  }
}
const SitereportHOC = withStyles(styles)(Sitereport);
export default connect(mapStateToProps, mapDispatchToProps)(SitereportHOC);
