import React, { Component } from 'react';
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import FormLabel from "@material-ui/core/FormLabel";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import InputAdornment from "@material-ui/core/InputAdornment";
import Radio from "@material-ui/core/Radio";
import Checkbox from "@material-ui/core/Checkbox";
import axios from 'axios';
// @material-ui/icons
import MailOutline from "@material-ui/icons/MailOutline";
import Check from "@material-ui/icons/Check";
import Clear from "@material-ui/icons/Clear";
import Contacts from "@material-ui/icons/Contacts";
import FiberManualRecord from "@material-ui/icons/FiberManualRecord";

// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardText from "components/Card/CardText.js";
import CardIcon from "components/Card/CardIcon.js";
import CardBody from "components/Card/CardBody.js";
import { withStyles } from '@material-ui/styles';
import {SERVER_URL} from "../../variables/constants";
import styles from "assets/jss/material-dashboard-pro-react/views/regularFormsStyle";
import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps } from '../../utils/MapStateDispatchProps.js'

const useStyles = makeStyles(styles);
// const classes = useStyles();

class BankDetailsForms extends Component {

	constructor(props) {
        super(props);
        this.state = {
			payeeName : '',
			bankName : '',
			bankAccountNumber : '',
			bankAddress : '',
			ifscCode : '',
			routingNumber : ''
		};
	}
	componentDidMount() {
    const { classes } = this.props;
    console.log('inside submit EditRole');
    const USER_ID = this.props.data.id;
    const TOKEN = 'Bearer '.concat(this.props.data.token);
    axios.defaults.headers.common['Authorization'] = TOKEN;
		axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Authorization';
			var url = `${SERVER_URL}/api/bankdetails/${USER_ID}`;
    		axios.get(url).then(response => response.data)
    		.then((data) => {
      				console.log(data);
                    this.setState({
						payeeName : data.payeeName,
						bankName : data.bankName,
						bankAccountNumber : data.accountNumber,
						bankAddress : data.address,
						ifscCode : data.ifscCode,
						routingNumber : data.routingNumber,
						isEditEnabled : false						
					});
     		}).catch((error) => console.log(error));
	}
	
	setPayeeName = (pPayeeName) =>
	{
		this.setState({payeeName:pPayeeName});
	}
	setBankName = (bBankName) =>
	{
		this.setState({bankName:bBankName});
	}
	setBankAccountNumber = (bBankAccountNumber) =>
	{
		this.setState({bankAccountNumber:bBankAccountNumber});
	}
	setBankAddress = (bBankAddress) =>
	{
		this.setState({bankAddress:bBankAddress});
	}
	setIfscCode = (iIfscCode) =>
	{
		this.setState({ifscCode:iIfscCode});
	}
	setRoutingNumber = (rRoutingNumber) =>
	{
		this.setState({routingNumber:rRoutingNumber});
	}
	
	setBankData = (pKeyName,pKeyValue) =>{
		this.setState({pKeyName:pKeyValue});
	}
	submitBankDetails = () =>
	{
		   
    const USER_ID = this.props.data.id;
    const TOKEN = 'Bearer '.concat(this.props.data.token);
    axios.defaults.headers.common['Authorization'] = TOKEN;
		axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Authorization';
		var url = `${SERVER_URL}/api/bankdetails/${USER_ID}`;
		var lData = {};
		lData.payeeName = this.state.payeeName;
		lData.bankName =  this.state.bankName;
		lData.accountNumber = this.state.bankAccountNumber;
		lData.address = this.state.bankAddress;
		lData.ifscCode = this.state.ifscCode;
		lData.routingNumber = this.state.routingNumber;
		
		
		axios.post(url,lData).then(response =>{
			 console.log(response);
		}).catch((error) => console.log(error));
	}
	
	editBankDetails = () =>
	{
    const USER_ID = this.props.data.id;
    const TOKEN = 'Bearer '.concat(this.props.data.token);
    axios.defaults.headers.common['Authorization'] = TOKEN;
		axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Authorization';
		var url = `${SERVER_URL}/api/bankdetails/${USER_ID}`;
		var lData = {};
		
		lData.payeeName = this.state.payeeName;
		lData.bankName =  this.state.bankName;
		lData.accountNumber = this.state.bankAccountNumber;
		lData.address = this.state.bankAddress;
		lData.ifscCode = this.state.ifscCode;
		lData.routingNumber = this.state.routingNumber;
		
		
		axios.put(url,lData).then(response => response.data)
    		.then((data) => {
      				console.log(data);
                  
     		}).catch((error) => console.log(error));
	}
render() {
	console.log('render called');
	const {classes} = this.props;
  return <GridContainer>
      <GridItem xs={12} sm={12} md={12}>
        <Card>
         <CardHeader color="rose" icon>
            <CardIcon color="rose">
              <MailOutline />
            </CardIcon>
            <h4 className={classes.cardIconTitle}>Details</h4>
          </CardHeader>
          <CardBody>
            <form>
              <GridContainer>
              <GridItem xs={12} sm={12} md={3}>
                  <FormLabel>
                    Payee Name*
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={12} md={9}>
                  <CustomInput
                    id="payee_name"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      type: "text",
					  onChange: event => {
                            this.setPayeeName(event.target.value);
                      },
					  value : this.state.payeeName
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={3}>
                  <FormLabel >
                    Bank Name*
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={12} md={9}>
                  <CustomInput
                    id="bank_name"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      type: "text",
                      onChange: event => {
                            this.setBankName(event.target.value);
                      },
					  value : this.state.bankName
					  
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={3}>
                  <FormLabel >
                    Bank Account Number*
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={12} md={9}>
                  <CustomInput
                    id="bank_account_number"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      type: "text",
                      onChange: event => {
                            this.setBankAccountNumber(event.target.value);
                      },
					  value : this.state.bankAccountNumber
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={3}>
                  <FormLabel  >
                    Bank Address*
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={12} md={9}>
                  <CustomInput
                    id="bank_address"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      type: "text",
                      onChange: event => {
                            this.setBankAddress(event.target.value);
                      },
					  value : this.state.bankAddress
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={3}>
                  <FormLabel >
                    Routing Number**
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={12} md={9}>
                  <CustomInput
                    id="routing_number"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      type: "text",
                      onChange: event => {
                            this.setRoutingNumber(event.target.value);
                      },
					  value : this.state.routingNumber
                    }}
                  />
                </GridItem>
              </GridContainer>
              <GridContainer>
                <GridItem xs={12} sm={12} md={3}>
                  <FormLabel  >
                    Swift Code**
                  </FormLabel>
                </GridItem>
                <GridItem xs={12} sm={12} md={9}>
                  <CustomInput
                    id="swift_code"
                    formControlProps={{
                      fullWidth: true
                    }}
                    inputProps={{
                      type: "text",
                      onChange: event => {
                            this.setIfscCode(event.target.value);
                      },
					  value : this.state.ifscCode
                    }}
                  />
                </GridItem>
              </GridContainer>
              
              
              
              <Button color="rose" onClick={() => this.submitBankDetails()}>Submit</Button>
	              <Button color="rose" onClick={() => this.editBankDetails()}>Edit</Button>
              

            </form>
          </CardBody>
        </Card>
      </GridItem>
                 
    </GridContainer>
}
}

const BankDetailsFormHOC = withStyles(styles)(BankDetailsForms);
export default connect(mapStateToProps, mapDispatchToProps)(BankDetailsFormHOC);
