import React from "react";
// react component for creating dynamic tables
import ReactTable from "react-table";

// @material-ui/core components
import { withStyles,makeStyles } from "@material-ui/core/styles";


import View from "@material-ui/icons/Visibility";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js"; 
import CardIcon from "components/Card/CardIcon.js";
import CardHeader from "components/Card/CardHeader.js";
import { dataTable } from "variables/Publisherdatatable";
import Switch from '@material-ui/core/Switch';
import { cardTitle } from "assets/jss/material-dashboard-pro-react.js";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Dialog from '@material-ui/core/Dialog';
import Slide from '@material-ui/core/Slide';
import MButton from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import Tooltip from '@material-ui/core/Tooltip';
// material-ui icons
import IconButton from '@material-ui/core/IconButton';
import Publishicon from "@material-ui/icons/Group";
import Globe from "@material-ui/icons/Public";
import Bankicon from "@material-ui/icons/AccountBalance";
import Profileicon from "@material-ui/icons/Person"
import LeftAorrow from '@material-ui/icons/KeyboardArrowLeft';
import Mappingicon from '@material-ui/icons/LinearScale';

const LightTooltip = withStyles((theme) => ({
  tooltip: {
    backgroundColor: theme.palette.common.white,
    color: 'rgba(0, 0, 0, 0.87)',
    boxShadow: theme.shadows[1],
    fontSize: 11,
  },
}))(Tooltip);
const styles = {
  cardIconTitle: {
    ...cardTitle,
    marginTop: "15px",
    marginBottom: "0px"
  },
  rootslider :{ 
    '& .MuiToolbar-gutters' :{
      marginLeft:"24px",
      marginRight:"24px"
    }, 
   '& .makeStyles-SliderTitle-167  ' :{
     fontSize:"18px!important"
  },
      '& .MuiFormControlLabel-root' :{ 
          paddingTop:"6px!important" ,
          marginRight:"15px!important",
         
         },
         '& .MuiTypography-body1' :{
          fontSize:"15px"
         },
      
      '& .MuiGrid-container' :{
        paddingLeft:"6px",
        paddingRight:"6px"
      },
      
      '& .MuiInputLabel-outlined.MuiInputLabel-shrink' :{
        transform:"translate(14px, -6px) scale(0.75)"
      },
      '& .MuiInputLabel-outlined' : { 
        transform: "translate(14px, 14px) scale(1)",
        fontSize:"0.9rem !important"
      }, 
      '& .MuiOutlinedInput-input':{
        paddingTop: "10px", 
         paddingBottom: "10px",
         paddingLeft: "10px",
      paddingRight:"10px"
      },
      '& .MuiFormControl-root ' :{
        width: "100%",
        marginTop:"10px",
        marginBottom:"10px",
        marginLeft:"0px",
        marginRight:"0px",
       
    
    },
    '& .makeStyles-grid-166' :{
      padding: "0 5px !important"
    },
    '& .MuiOutlinedInput-adornedStart' :{
      paddingLeft:"10px"
    },
    '& .makeStyles-grid-167' :{
      padding: "0 5px !important"
    },
    
    
    '& #document-type' :{
      textAlign:"center!important"
    },
      '& .MuiButton-label' :{
          fontSize:"0.875rem",
          fontWeight:"bold"
      },
      '& .MuiPaper-root':{
          backgroundColor: "#dddddd!important"
        },
      '& .MuiAppBar-positionFixed':{
          top:"0",
          left: "auto",
          right: "0",
          position:" relative!important",
          boxShadow:"none!important"
       
      },
      
        '& .MuiToolbar-regular': {
          minHeight:"64px!important",
          overflowX:" hidden !important"
      },
      '& .MuiDialog-paperFullScreen' :{
          backgroundColor: "#dddddd!important",
          overflowX:" hidden !important"
      },
      '& .MuiPaper-elevation4' :{
          borderBottom:"1px solid rgb(0 0 0 / 10%) !important",
          background: "#ebebeb !important",
          boxShadow:"none!important"
      }
  },
  CardHeading:{
    textAlign:"center",
    color:"black!important",
},
MainHeading:{
    textAlign:"left",
    color:"#3c4858!important",
    
},
CloseButton :{
    backgroundColor:"white",
    borderRadius:"30px",
    color:"black",
    width: "41px",
    height: "41px", 
    
    minWidth: "41px", 
    paddingLeft: "12px",
    paddingRight: "12px"
},
SliderTitle :{
    fontSize:"16px",
    paddingLeft:"30px",
    color:"black !important",
    fontWeight:"400"

},
CustomappBar :{
    display: "flex",
    flex:" 0 0 64px",
    background: "#ebebeb",
    height:" 64px",
    borderBottom: "1px solid rgb(0 0 0 / 10%)",
    
   
},
heading:{
    color:"black!important"
  },
SliderBackground :{
    backgroundColor: "#dddddd!important"
},
TextCenter:{
    textAlign:"center"
},
SmallText:{
    fontSize:"12px",
    color:"#000000de",
    fontWeight:"300"
},
headingStyle: {
    marginBottom: "0px",
    fontSize: "1.1em",
    fontWeight: "500",
    marginTop: "25px",
  },
root2: {
    
      float: "right"
    },
    checkboxroot :{
        '& .MuiSvgIcon-root' :{
          width: "0.7em",
          height:"0.7em"
         },
      },
    ActionsButton :{
        '& .MuiButton-root' :{
            minWidth:" 0px"
        },
        '& .MuiButton-root' :{
            height: "0px"
        }
        
    }
};
 
const useStyles = makeStyles(styles);
const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="left" ref={ref} {...props} />;
  });
export default function Publisher() {
const [open, setOpen] = React.useState(false);
  const [data, setData] = React.useState(
    dataTable.dataRows.map((prop, key) => {
      return {
        id: key,
        publisherName: prop[0],
        contactPersonName: prop[1],
        contactPersonNumber: prop[2],
        email: prop[3],
        benid: prop[4],
        actions: prop[5],
      };
    })
  );
  const classes = useStyles();
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false); 
  };
  return (
      
    <GridContainer>
      
      <GridItem lg={12}>
        <Card>
          <CardHeader color="primary" icon>
          <CardIcon color="primary">
         <Publishicon style={{color:"white"}} />
         
         </CardIcon>
            <h4 className={classes.cardIconTitle} style={{marginTop:"0px!important"}} >Publishers</h4>
            
          </CardHeader>
          <CardBody> 
            <ReactTable
              data={data}
              filterable
              columns={[
                {
                    Header: "Publisher Name",
                    accessor: "publisherName",
                    Filter: ({filter, onChange}) => (
                        <input type='text' style={{textAlign:'center'}}
                               placeholder="Search Name"
                               value={filter ? filter.value : ''}
                                   onChange={event => onChange(event.target.value)}
                        />
                      ),
                },
                {
                    Header: "Contact Person",
                    accessor: "contactPersonName",
                    Filter: ({filter, onChange}) => (
                        <input type='text' style={{textAlign:'center'}}
                               placeholder="Search Person"
                               value={filter ? filter.value : ''}
                                   onChange={event => onChange(event.target.value)}
                        />
                      ),
                },
                {
                    Header: "Phone Number",
                    accessor: "contactPersonNumber",
                    Filter: ({filter, onChange}) => (
                        <input type='text' style={{textAlign:'center'}}
                               placeholder="Search Phone Number"
                               value={filter ? filter.value : ''}
                                   onChange={event => onChange(event.target.value)}
                        />
                      ),
                },
                {
                    Header: "Email",
                    accessor: "email",
                    Filter: ({filter, onChange}) => (
                        <input type='text' style={{textAlign:'center'}}
                               placeholder="Search Email Id"
                               value={filter ? filter.value : ''}
                                   onChange={event => onChange(event.target.value)}
                        />
                      ),
                },
                {
                    Header: "BEN ID",
                    accessor: "benid",
                    Filter: ({filter, onChange}) => (
                        <input type='text' style={{textAlign:'center'}}
                               placeholder="Search ID"
                               value={filter ? filter.value : ''}
                                   onChange={event => onChange(event.target.value)}
                        />
                      ),
                }, 
                {
                    Header: "Actions",
                    accessor: "actions",
                    Cell: id => (
                        <div className={classes.ActionsButton}>
                    <LightTooltip  title="View" aria-label="view"  placement="top">
                        <MButton onClick={handleClickOpen}
                        justIcon
                        round
                        simple
                        color="secondary"
                          className="view"
                        
                      >
                        <View />
                      </MButton>
                      </LightTooltip>
                      <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition} className={classes.rootslider}>
                    <AppBar className={classes.CustomappBar}>
                    <Toolbar>
                        <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close" className={classes.CloseButton}>
                        <LeftAorrow />
                        </IconButton>
                        <h4 className={classes.SliderTitle}>
                      Publisher Profile View
                        </h4>
                    
                    </Toolbar>
                    </AppBar>
                    <GridContainer style={{paddingTop:"3%"}}>
  <GridItem lg={2}></GridItem>
  <GridItem xs={12} md={8} lg={8} >
    <Card>
      <CardHeader color="primary" icon>
        <CardIcon color="primary">
          <Profileicon />
        </CardIcon>
        <h4 className={classes.heading}>Profile</h4>
      </CardHeader>
      <CardBody className={classes.CardBody}>
        <h4 className={classes.headingStyle}>Primary Information</h4>
        <GridContainer className={classes.gridcontainer} style={{ margitnBottom: "15px" }} >
          <GridItem lg={6} xs={12} md={6} className={classes.textfieldsgrid}>
            <TextField className={classes.textfields} disabled={true}  label="Name of the entity" variant="outlined" id="outlined-size-small" size="small" style={{ width: "100%" }} />
          </GridItem>
          <GridItem lg={3} xs={6} md={3} className={classes.textfieldsgrid}>
            <TextField className={classes.textfields} disabled={true}  label="GST No" variant="outlined" id="outlined-size-small" size="small" />
          </GridItem>
          <GridItem lg={3} xs={6} md={3} className={classes.textfieldsgrid}>
            <TextField className={classes.textfields} label="PIN" disabled="true" variant="outlined" id="outlined-size-small" size="small" />
        </GridItem>
        </GridContainer>
        <h4 className={classes.headingStyle}>Address Details</h4>

        <GridContainer className={classes.gridcontainer}>
          <GridItem lg={6} xs={12} md={6} className={classes.textfieldsgrid}>
            <TextField className={classes.textfields} label="Address line1 " disabled={true} variant="outlined" id="outlined-size-small" size="small" style={{ width: "100%" }} />
          </GridItem>
          <GridItem lg={6} xs={12} md={6} className={classes.textfieldsgrid}>
            <TextField className={classes.textfields} label="Address line2" disabled={true} variant="outlined" id="outlined-size-small" size="small" style={{ width: "100%" }} />
          </GridItem>
        </GridContainer>

        <GridContainer className={classes.gridcontainer}>

          <GridItem lg={3} xs={6} md={3} className={classes.textfieldsgrid}>
            <TextField className={classes.textfields} label="Post Office" disabled={true} variant="outlined" id="outlined-size-small" size="small" style={{ width: "100%" }} />
          </GridItem>
          <GridItem lg={3} xs={6} md={3} className={classes.textfieldsgrid}>
            <TextField className={classes.textfields} className={classes.taluk}  disabled={true} label="Taluk" variant="outlined" id="outlined-size-small" size="small" />
          </GridItem>
          <GridItem lg={3} xs={6} md={3} className={classes.textfieldsgrid}>
            <TextField className={classes.textfields} label="District" disabled={true} variant="outlined" id="outlined-size-small" size="small" />
          </GridItem>
          <GridItem lg={3} xs={6} md={3} className={classes.textfieldsgrid}>
            <TextField className={classes.textfields} label=" State "  disabled={true} variant="outlined" id="outlined-size-small" size="small" />
          </GridItem>
        </GridContainer>
      </CardBody>

    </Card>
  </GridItem>
  <GridItem lg={2}></GridItem>
</GridContainer>
<GridContainer >
  <GridItem lg={2} md={2}></GridItem>
  <GridItem lg={8} md={8}>
    <Card>
      <CardHeader color="primary" icon>
        <CardIcon color="primary">
          <Globe />
        </CardIcon>
        <h4 className={classes.heading}>Claim your websites</h4>
      </CardHeader>
      <CardBody>
      <GridContainer className={classes.gridcontainer}>

<GridItem lg={2} md={2} className={classes.textfieldsgrid} >
  <TextField className={classes.textfieldslno} style={{ textAlign: "center !important" }}
    id="document-type"
    disabled="true"
    value="1"
    label="S No"
    variant="outlined" />
   

</GridItem>
<GridItem lg={10} md={10} className={classes.textfieldsgrid}>
  <TextField className={classes.textfields} label="Website URL" disabled={true} variant="outlined" id="outlined-size-small" size="small" style={{ width: "100%" }} />
</GridItem>
</GridContainer>
      </CardBody>
    </Card>
  </GridItem>
  <GridItem lg={2} md={2}></GridItem>
</GridContainer>
<GridContainer className={classes.gridcontainer}>

  <GridItem lg={2}></GridItem>
  <GridItem xs={12} md={8} lg={8}>
    <Card>
      <CardHeader color="primary" icon>
        <CardIcon color="primary">
          <Bankicon />
        </CardIcon>
        <h4 className={classes.heading}>Bank Account details</h4>
      </CardHeader>
      <CardBody className={classes.CardBody}>
      <GridContainer className={classes.gridcontainer}>
    <GridItem lg={12} className={classes.InsideCard} style={{ padding: "0px 5px!important" }} >
      <Card  >
        <CardBody className={classes.CardBody}>
          <GridContainer className={classes.gridcontainer}>
          <GridItem lg={6} md={6} className={classes.textfieldsgrid}>
              <TextField className={classes.textfields} label="Bank Account Number" type="password" disabled="true" variant="outlined" id="outlined-size-small" size="small" style={{ width: "100%" }} />
            </GridItem>
            <GridItem lg={6} md={6} className={classes.textfieldsgrid}>
              <TextField className={classes.textfields} label="Beneficiary ID" type="text" variant="outlined"  id="outlined-size-small" size="small" style={{ width: "100%" }} />
            </GridItem>
          </GridContainer>
          <GridContainer className={classes.gridcontainer}>
          <GridItem lg={4} className={classes.textfieldsgrid}>
              <TextField className={classes.textfields} label="Nick Name" variant="outlined" disabled="true" id="outlined-size-small" size="small" style={{ width: "100%" }} />
            </GridItem>
            <GridItem lg={4} className={classes.textfieldsgrid}>
              <TextField className={classes.textfields} label="IFSC" variant="outlined" disabled="true" id="outlined-size-small" size="small" style={{ width: "100%" }} />
            </GridItem>
            <GridItem lg={4} className={classes.textfieldsgrid}>
              <TextField className={classes.textfields} label="SWIFT Code" variant="outlined" disabled="true" id="outlined-size-small" size="small" style={{ width: "100%" }} />
            </GridItem>
           
          </GridContainer>
          
        </CardBody>
      </Card>
    </GridItem>
  </GridContainer>
        
      </CardBody >

    </Card>
  </GridItem>
  <GridItem lg={2}>

  </GridItem>
</GridContainer>
<GridContainer className={classes.gridcontainer}>

  <GridItem lg={2}></GridItem>
  <GridItem xs={12} md={8} lg={8}>
    <Card>
      <CardHeader color="primary" icon>
        <CardIcon color="primary">
          <Mappingicon />
        </CardIcon>
        <h4 className={classes.heading}>Website - Bank Account Mapping</h4>
      </CardHeader>
      <CardBody className={classes.CardBody}>
      <GridContainer className={classes.gridcontainer}>
    <GridItem lg={12} className={classes.InsideCard} style={{ padding: "0px 5px!important" }} >
      <Card>
        <CardBody className={classes.CardBody}>
          <GridContainer className={classes.gridcontainer}>
            <GridItem lg={1} md={1} className={classes.textfieldsgrid}>
              <TextField className={classes.textfieldslno}
                id="document-type"
                value="1"
                disabled="true"
                readOnly="true"
                label="S No"
                variant="outlined" />

            </GridItem>
            <GridItem lg={11} md={11} className={classes.textfieldsgrid}>
              <TextField className={classes.textfields} label=" Website Name" disabled="true" variant="outlined" id="outlined-size-small" size="small" style={{ width: "100%" }} disabled="true" />
            </GridItem>
           
          </GridContainer>
         <GridContainer>
         <GridItem lg={12} md={12}  className={classes.checkboxroot} >
              
              <RadioGroup row aria-label="nickname" name="nickname" defaultValue="Nickname1" >
            <FormControlLabel value="Nickname1" control={<Radio />} label="Nickname1" disabled="true" />
            <FormControlLabel value="Nickname2" control={<Radio />} label="Nickname2" disabled="true" />
            <FormControlLabel value="Nickname3" control={<Radio />} label="Nickname3" disabled="true" />
            <FormControlLabel value="Nickname4" control={<Radio />} label="Nickname4" disabled="true" />
            <FormControlLabel value="Nickname5" control={<Radio />} label="Nickname5" disabled="true" />
            </RadioGroup>
              </GridItem>
         </GridContainer>
        </CardBody>
      </Card>
    </GridItem>
  </GridContainer>
        <GridContainer >
          <GridItem xs={12} md={12} lg={12} className={classes.textfieldsgrid}>
            
          </GridItem>
        </GridContainer>
      </CardBody >

    </Card>
  </GridItem>
  <GridItem lg={2}>

  </GridItem>
</GridContainer>
      </Dialog>
                      {" "}
                      <LightTooltip  title="Disable / Unable" aria-label="disableunable"  placement="top">
                    <Switch
                        
                        name="Disableunable"
                        inputProps={{ 'aria-label': 'secondary checkbox' }}
                        size="small"
                    />
                    </LightTooltip>
                    </div>
                    ),
                    sortable: false,
                    filterable: false
                }
            ]}
            defaultPageSize={5}
            showPaginationTop
            showPaginationBottom={false}
            className="-highlight"
            />
          </CardBody>
          <CardHeader style={{float:"right!important"}}>
            
            <MButton color="secondary" variant="outlined" style={{float:"right"}}>
           Close
           </MButton> 
          </CardHeader>
        </Card>
      </GridItem>
     
    </GridContainer>
  );
}
