import React, { Component } from 'react'
import { makeStyles } from "@material-ui/core/styles";

// material-ui icons
import Assignment from "@material-ui/icons/Assignment";
import axios from 'axios';
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardIcon from "components/Card/CardIcon.js";
import CardBody from "components/Card/CardBody.js";
import { SERVER_URL } from "../../variables/constants";
import { cardTitle } from "assets/jss/material-dashboard-pro-react.js";
import Button from "components/CustomButtons/Button.js";

import { withStyles } from '@material-ui/styles';
import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps } from '../../utils/MapStateDispatchProps.js'


const styles = {
    customCardContentClass: {
        paddingLeft: "0",
        paddingRight: "0"
    },
    cardIconTitle: {
        ...cardTitle,
        marginTop: "15px",
        marginBottom: "0px"
    }
};

const useStyles = makeStyles(styles);
export class Websites extends Component {
    state = {
        websiteHeaders : ["Name","Url" , "Http Enabled" , "Https Enabled","Edit"],
        websiteData: []
    }

    editWebsite = (id) =>{
            //console.log("edit website" + id);
    }
    

    componentDidMount() {
        const classes = this.props.classes;
        const USER_ID = this.props.data.id;
        const TOKEN = 'Bearer '.concat(this.props.data.token);

        axios.defaults.headers.common['Authorization'] = TOKEN;
        axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Authorization';
        

        axios.get(`${SERVER_URL}/api/userwebsite/user/${USER_ID}`, { headers: { "Authorization": TOKEN } })
            .then(res => {
                let lWebsiteData = [];
                res.data.forEach(item =>{
                    let lWebsite = [];
                    lWebsite.push(item.name);
                    lWebsite.push(item.hostURL);
                    item.httpEnabled ? lWebsite.push("Yes") : lWebsite.push("No");
                    item.httpsEnabled ? lWebsite.push("Yes") : lWebsite.push("No");
                    lWebsite.push(
                    <Button round  color = "red" className={classes.actionButton + " " + classes.actionButtonRound} key={item.id} onClick={() => this.editWebsite(item.id)}>
                         Edit
                    </Button>
                    );
                    lWebsiteData.push(lWebsite);
                });
                this.setState({websiteData:lWebsiteData});
            }).catch(function (error) {
                console.log(error);
            });
    }
    render() {


        const classes = this.props.classes;
        return (
            <GridContainer>
                <GridItem xs={12}>
                    <Card>
                        <CardHeader color="rose" icon>
                            <CardIcon color="rose">
                                <Assignment />
                            </CardIcon>
                            <h4 className={classes.cardIconTitle}>Websites</h4>
                        </CardHeader>
                        <CardBody>
                            <Table
                                tableHeaderColor="primary"
                                tableHead={this.state.websiteHeaders}
                                tableData={this.state.websiteData}
                                coloredColls={[3]}
                                colorsColls={["primary"]}
                            />
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer >
        )
    }
}

const WebsitesHOC = withStyles(styles)(Websites);
export default connect(mapStateToProps, mapDispatchToProps)(WebsitesHOC);