import React, { Component } from 'react'
import ReactTable from "react-table";
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardIcon from "components/Card/CardIcon.js";
import CardHeader from "components/Card/CardHeader.js";


import Assignment from "@material-ui/icons/Assignment";
import Dvr from "@material-ui/icons/Dvr";
import Close from "@material-ui/icons/Close";
import Button from "components/CustomButtons/Button.js";
import axios from "axios";
import {SERVER_URL} from "../../variables/constants";

import { withStyles } from '@material-ui/styles';
import { connect } from 'react-redux';
import { mapStateToProps, mapDispatchToProps } from '../../utils/MapStateDispatchProps.js'
import { makeStyles } from "@material-ui/core/styles";
import { cardTitle } from "assets/jss/material-dashboard-pro-react.js";
const styles = {
    customCardContentClass: {
        paddingLeft: "0",
        paddingRight: "0"
    },
    cardIconTitle: {
        ...cardTitle,
        marginTop: "15px",
        marginBottom: "0px"
    }
};

const useStyles = makeStyles(styles);

export class Publisher extends Component {
    state = {
    		publishers : []
    }
    
    componentDidMount() {
        
        const USER_ID = this.props.data.id;
        const TOKEN = 'Bearer '.concat(this.props.data.token);

        axios.defaults.headers.common['Authorization'] = TOKEN;
        axios.defaults.headers.common['Access-Control-Allow-Headers'] = 'Authorization';
        
    	axios.get(`${SERVER_URL}/api/users/publishers`).then(response => response.data)
    	.then((data) => {
    		this.setState({ publishers: data})
    	}).catch((error) => {
            console.error(error);
        });
    }
	
    editAdvertiser = (editObject) => {
    	this.props.history.push('/admin/edit-user',{editObject});
    }

    disableAdvertiser = (pKey) => {
    	console.log('id for delete', pKey);
     	
    	var url = `${SERVER_URL}/api/users/${pKey}` ;
    	axios.delete(url, {}).then(response => response.data)
		  .then((data) => {
		  this.setState({ publishers: data})
		  }).catch(error => {console.log(error);})
    	
    }


    render() {
        //var classes = useStyles();
        return <GridContainer>
                <GridItem xs={12}>
                    <Card>
                        <CardHeader color="primary" icon>
                            <CardIcon color="primary">
                                <Assignment />
                            </CardIcon>
                            <h4>React Table</h4>
                        </CardHeader>
                        <CardBody>
                            <ReactTable
                                data={this.state.publishers}
                                filterable
                                columns={[
                                    {
                                        Header: "Company Name",
                                        accessor: "companyName"
                                    },
                                    {
                                        Header: "Contact Person",
                                        accessor: "contactPersonName"
                                    },
                                    {
                                        Header: "Phone Number",
                                        accessor: "contactPersonNumber"
                                    },
                                    {
                                        Header: "Email",
                                        accessor: "email"
                                    },
                                    {
                                        Header: "Edit/ Delete",
                                        accessor: "id",
                                        Cell: id => (
                                        		<div className="actions-right">
                                        		<Button
                                                justIcon
                                                round
                                                simple
                                                onClick={() => {
                                                  let obj = this.state.publishers.find(o => o.id === id.original.id);
                                                  this.editAdvertiser(obj);
                                                }}
                                        		color="warning"
                                                  className="edit"
                                              >
                                                <Dvr />
                                              </Button>
                                              {" "}
                                            {/* use this button to remove the data row */}
                                            <Button
                                              justIcon
                                              round
                                              simple
                                              onClick={() => {
                                                  this.disableAdvertiser(id.original.id);
                                              }}
                                              color="danger"
                                              className="remove"
                                            >
                                              <Close />
                                            </Button>{" "}
                                            </div>
                                        ),
                                        sortable: false,
                                        filterable: false
                                    }
                                ]}
                                defaultPageSize={10}
                                showPaginationTop
                                showPaginationBottom={false}
                                className="-striped -highlight"
                            />
                        </CardBody>
                    </Card>
                </GridItem>
            </GridContainer>
        
    }
}

const PublisherHOC = withStyles(styles)(Publisher);
export default connect(mapStateToProps, mapDispatchToProps)(PublisherHOC);

